import React, { createContext, useEffect } from "react";
import { AuthService } from '../services/Auth.service';
import useStorage  from '../customHooks/use-storage';

export const AuthContext = createContext({
  token: '',
  handleLogin: async () => { return new Promise(() => true); },
  handleLogout: async () => {},
  verifyToken: async () => {}
});


export const AuthProvider = ({ children }) => {
  const [token, setToken] = useStorage('token');
  const verifyToken = () => {
    if (token === null || token === '') {
      return;
    }
    AuthService.verifyToken(token).then((res) => {
      console.log('token verified');
    }).catch((e) => {
      console.log('invalid token found', token);
      setToken('');
    });
  };
  const handleLogin = async (user, pass) => {
    return new Promise( (resolve, reject) => {
      AuthService.login(user, pass).then(
        (usr) => {
          setToken(usr.token);
          resolve(true);
        },
        () => {
          setToken('');
          reject(false);
        }
      );
    });
  };
  const handleLogout = () => {
    setToken('');
  };

 useEffect(() => {
  verifyToken();
 }, []);
  return (
    <AuthContext.Provider value={{ token, handleLogin, handleLogout, verifyToken }}>
      {children}
    </AuthContext.Provider>
  );
};
